import React, { PureComponent } from 'react';
import Types from 'prop-types';

import Select from '../select';
import {
  explodeNumberModel,
  filterOptionsForQuery,
  isValidPhoneNumber,
  cleanNumber,
} from './utils';

import countries from './data/countries';

const ALLOWED_PHONE_CHARS = /^$|^[\d-\s]+$/;

class PhoneNumberInput extends PureComponent {
  static propTypes = {
    required: Types.bool,
    disabled: Types.bool,
    value: Types.string,
    onChange: Types.func.isRequired,
    locale: Types.string,
    searchPlaceholder: Types.string,
    size: Types.oneOf(['sm', 'md', 'lg']),
    placeholder: Types.string,
  };

  static defaultProps = {
    required: false,
    disabled: false,
    value: null,
    locale: 'en-GB',
    searchPlaceholder: 'Prefix',
    size: 'md',
    placeholder: '',
  };

  constructor(props) {
    super(props);
    const { value, locale } = this.props;
    const cleanValue = value ? cleanNumber(value) : null;
    const { prefix, suffix } = explodeNumberModel(cleanValue, locale);
    const previousReturned = isValidPhoneNumber(value) ? prefix + suffix : null;

    this.state = {
      suffix,
      prefix,
      previousReturned,
      locale, // eslint-disable-line
      searchQuery: '',
    };
  }

  static getDerivedStateFromProps(nextProps, state) {
    const { value, locale } = nextProps;

    if (locale !== state.locale) {
      if (!state.suffix) {
        const { prefix } = explodeNumberModel('', locale);
        return {
          ...state,
          prefix,
          locale: nextProps.locale,
        };
      }
      return {
        ...state,
        locale: nextProps.locale,
      };
    }

    const cleanValue = value ? cleanNumber(value) : null;
    const valueToCheck = isValidPhoneNumber(cleanValue) ? cleanValue : null;

    if (valueToCheck !== state.previousReturned) {
      const { prefix, suffix } = explodeNumberModel(cleanValue, state.locale);
      let previousReturned = null;
      if (isValidPhoneNumber(cleanValue)) {
        previousReturned = prefix + suffix;
      }

      nextProps.onChange(previousReturned);
      return {
        ...state,
        suffix,
        prefix,
        previousReturned,
        searchQuery: '',
      };
    }

    return null;
  }

  onChangeSearch = searchQuery => {
    this.setState({ searchQuery });
  };

  getSelectOptions = () => {
    const options = [];
    const filteredOptions = this.state.searchQuery
      ? filterOptionsForQuery(countries, this.state.searchQuery)
      : countries;
    filteredOptions.forEach(option => {
      const { phone, iso3, iso2 } = option;
      options.push({ value: phone, label: phone, note: iso3 || iso2 });
    });
    return options;
  };

  handleChangeSelect = event => {
    const { suffix } = this.state;
    const prefix = event.value;

    this.setState({ searchQuery: '', prefix }, () => {
      const value = cleanNumber(prefix + suffix);
      this.returnValue(value);
    });
  };

  handleInputChange = event => {
    const suffix = event.target.value;
    if (ALLOWED_PHONE_CHARS.test(suffix)) {
      const { prefix } = this.state;

      this.setState({ suffix }, () => {
        const value = cleanNumber(prefix + suffix);
        this.returnValue(value);
      });
    }
  };

  returnValue = value => {
    const previousReturned = isValidPhoneNumber(value) ? value : null;
    if (previousReturned !== this.state.previousReturned) {
      this.setState({ previousReturned }, () => {
        this.props.onChange(previousReturned);
      });
    }
  };

  render() {
    const { searchPlaceholder, disabled, required, size, placeholder } = this.props;
    const selectOptions = this.getSelectOptions();
    const { prefix, suffix } = this.state;

    return (
      <div className="tw-telephone">
        <div className="row">
          <div className="col-xs-5">
            <Select
              options={selectOptions}
              selected={{ value: prefix, label: prefix }}
              onChange={this.handleChangeSelect}
              searchPlaceholder={searchPlaceholder}
              onSearchChange={this.onChangeSearch}
              searchValue={this.state.searchQuery}
              required={required}
              disabled={disabled}
              size={size}
            />
          </div>

          <div className="col-xs-7">
            <div className={`input-group input-group-${size}`}>
              <input
                name="phoneNumber"
                value={suffix}
                type="text"
                className="form-control"
                disabled={disabled}
                onChange={this.handleInputChange}
                required={required}
                placeholder={placeholder}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default PhoneNumberInput;



